/*退货管理main组件*/
<template>
  <div class="mainCon">
    <div class="mainTop">
      <div class="mainTopLeft">
        <el-button type="primary" size="mini" @click="addWorkOrder"><i class="el-icon-plus "></i>添加</el-button>
        <el-button type="warning" size="mini" @click="changeWorkOrder"><i class="el-icon-edit "></i>修改</el-button>
        <el-button type="danger" size="mini" @click="deleteWorkOrder"><i class="el-icon-close "></i>删除</el-button>
        <el-button type="success" icon="el-icon-refresh" size="mini">刷新</el-button>
      </div>
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-sizes="[50, 100, 200]"
          :page-size="50"
          :pager-count="pagerCount"
          layout="sizes, prev, pager, next"
          :total="total"
        ></el-pagination>
      <div class="common">共{{ productNum }}笔</div>
    </div>
    <div class="mainBottom">
      <el-table
        :data="tableData"
        style="width: 100%"
        stripe
        border
        v-loading='tableLoading'
        @selection-change="handleSelectionChange">
        <el-table-column
          fixed
          type="index"
          min-width="50">
        </el-table-column>
        <el-table-column
          fixed
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          fixed
          prop="goods_name"
          label="商品名称"
          min-width="100">
        </el-table-column>
        <el-table-column
          fixed
          label="商品图片"
          min-width="80">
            <template slot-scope="scope">
              <span v-show="scope.row.goods_imgs.length>0">
                <el-tooltip placement="right-start" effect="light">
                  <div slot="content">
                    <img v-lazy="bigImgSrc" alt srcset class="bigImgCon">
                  </div>
                  <img v-if="scope.row.goods_imgs[0]" v-lazy="scope.row.goods_imgs[0].path" class="ImgCon" alt srcset ref="user_image" @mouseover="mouseover(scope.row.goods_imgs)" @mousewheel="wheel(scope.row.goods_imgs)">
                </el-tooltip>
              </span>
            </template>
        </el-table-column>
        <el-table-column
          fixed
          prop="sku"
          label="sku"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="tracking_num"
          show-overflow-tooltip
          label="运单号"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="return_reason_alias"
          show-overflow-tooltip
          label="退货原因">
        </el-table-column>
        <el-table-column
          prop="other_reason"
          show-overflow-tooltip
          label="退货其他原因"
          min-width="100">
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          prop="refunds"
          label="退款金额（美元）"
          min-width="130">
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          prop="solution_alias"
          label="解决方式"
          min-width="100">
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          prop="solution_alias"
          label="退货图片"
          min-width="80">
          <template slot-scope="scope">
            <span v-show="scope.row.refunds_imgs_alias.length>0">
              <el-tooltip placement="right-start" effect="light">
                <div slot="content">
                  <img v-lazy="scope.row.refunds_imgs_alias[0]" alt srcset class="bigImgCon">
                </div>
                <img v-if="scope.row.refunds_imgs_alias[0]" v-lazy="scope.row.refunds_imgs_alias[0]" class="ImgCon" alt srcset ref="user_image" @mouseover="mouseover(scope.row.refunds_imgs_alias[0])" @mousewheel="wheel(scope.row.refunds_imgs_alias[0])">
              </el-tooltip>
            </span>
          </template>
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          prop="solution_alias"
          label="退款截图"
          min-width="80">
          <template slot-scope="scope">
            <span v-show="scope.row.refunds_imgs_alias.length>0">
              <el-tooltip placement="right-start" effect="light">
                <div slot="content">
                  <img v-lazy="scope.row.refunds_imgs_alias[1]" alt srcset class="bigImgCon">
                </div>
                <img v-if="scope.row.refunds_imgs_alias[1]" v-lazy="scope.row.refunds_imgs_alias[1]" class="ImgCon" alt srcset ref="user_image" @mouseover="mouseover(scope.row.refunds_imgs_alias[1])" @mousewheel="wheel(scope.row.refunds_imgs_alias[1])">
              </el-tooltip>
            </span>
          </template>
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          prop="created_at"
          label="创建时间"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="note"
          show-overflow-tooltip
          label="备注"
          min-width="50">
        </el-table-column>
      </el-table>
    </div>
    <el-dialog title="编辑工单" :visible.sync="dialogFormVisible" width="50%" class="editWorkOrder">
      <el-form :model="ruleForm" size="mini" :rules="rules" ref="ruleForm">
        <el-form-item label="sku" :label-width="formLabelWidth" prop="sku">
          <el-input v-model="ruleForm.sku" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="运单号" :label-width="formLabelWidth" prop="tracking_num">
          <el-input v-model="ruleForm.tracking_num" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="退货原因类别" :label-width="formLabelWidth" prop="return_reason">
          <el-select v-model="ruleForm.return_reason" placeholder="请选择退货原因">
            <el-option v-for="(item,index) in returnReasonList" :key="index" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="退货其他原因" :label-width="formLabelWidth">
          <el-input v-model="ruleForm.other_reason" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="最终解决方式" :label-width="formLabelWidth" prop="solution">
          <el-select v-model="ruleForm.solution" placeholder="请选择最终解决方式">
            <el-option v-for="(item,index) in solutionList" :key="index" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="退款金额" :label-width="formLabelWidth" prop="refunds">
          <el-input v-model="ruleForm.refunds" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="退款/退货截图" :label-width="formLabelWidth">
          <uploadFile :uploadFileData="uploadFileConOne" />
        </el-form-item>

        <el-form-item label="备注" :label-width="formLabelWidth" prop="note">
          <el-input
            type="textarea"
            :rows="4"
            placeholder="请输入备注信息"
            v-model="ruleForm.note">
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogFormVisible = false">取 消</el-button>
        <el-button size="small" type="primary" @click="submit(submitState)">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import uploadFile from '@/components/uploadFile'
  let imgIndex = 0;//bigImg索引
export default {
  data(){
    let validateSKU = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入SKU'));
      } else {
        callback();
      }
    };
    let validateTransportNumber = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请填写运单号'));
      } else {
        callback();
      }
    };
    let validateReturnCategory = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请选择退货原因类别'));
      } else {
        callback();
      }
    };
    let validateSolution = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请选择最终解决方式'));
      } else {
        callback();
      }
    };
    let validateRefundAmount = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请填写退款金额'));
      } else {
        callback();
      }
    };
    return {
      tableLoading: false,
      bigImgSrc: '',
      total: 0,//该分类产品共有多少条数据（非共多少笔）
      productNum: 0,//共多少笔
      currentPage: 1,//第几页
      pagerCount: 5,//
      submitState: 'add',//添加/修改状态
      // tableColumnState: false,//表格展开行显示状态
      dialogFormVisible: false,//弹出框显示状态
      rules: {
        name: [  { required: true, message: '标题不能为空'} ],
      },
      tableData: [],
      imageList:[],//上传图片容器
      multipleSelection: [],
      activeName: 'first',//标签页默认显示
      ruleForm: {//数据临时操作容器
      sku: '',
      tracking_num:'',
      return_reason: '',
      other_reason: '',
      solution: '',
      refunds: '',
      note: '',
      image: '',
      },
      addRuleForm: {},//添加工单数据容器
      changeRuleForm: {},//修改工单数据容器
      formLabelWidth: "120px",
      rules: {
        sku: [ { validator: validateSKU, trigger: 'blur' } ],
        tracking_num: [ { validator: validateTransportNumber, trigger: 'blur' } ],
        return_reason: [ { validator: validateReturnCategory, trigger: 'change' } ],
        solution: [ { validator: validateSolution, trigger: 'change' } ],
        refunds: [ { validator: validateRefundAmount, trigger: 'blur' } ],
      },
      //退货图片+退款截图容器
      uploadFileConOne: {
        limit: 2,
        uploadSrc: 'https://jsonplaceholder.typicode.com/posts/',
        multiple: false,
        buttonState: false,
        imgList: [],
        comName: 'returnManagement'
      },
      returnReasonList: [
        { value: '1', label: '质量问题' },
        { value: '2', label: '物流问题' },
        { value: '3', label: '包装问题' },
        { value: '4', label: '色差问题' },
        { value: '5', label: '其他' },
      ],
      solutionList: [
        { value: '1', label: '全额退款' },
        { value: '2', label: '部分退款' },
        { value: '3', label: '补发' },
        { value: '4', label: '其他' },
      ]
    }
  },
   methods: {
     //添加工单
    addWorkOrder(){
      this.dialogFormVisible = true;
      this.submitState = 'add';
    },
    //修改工单
    changeWorkOrder(){
      if(this.multipleSelection.length != 1){
        this.$message({
          message: '请选择一条记录进行修改',
          type: 'warning'
        });
      }else {
        this.submitState = 'change';
        this.uploadFileConOne.imgList = [];
        this.dialogFormVisible = true;
        // ;
        this.ruleForm = this.multipleSelection[0];
        for (let index = 0; index < this.multipleSelection[0].refunds_imgs_alias.length; index++) {
          const element = this.multipleSelection[0].refunds_imgs_alias[index];
          this.uploadFileConOne.imgList.push({url: element});
        }
      }
    },
    //滚动查看图片
    wheel(imgValue,e=event) {
      this.bodyMouseWheel(e);
      //判断浏览器，谷歌|| firefox滑轮事件
      if (e.wheelDelta > 0 || e.detail > 0) {
        //当滑轮向上滚动时
        if(imgValue.length>=imgIndex&&imgIndex>=0){
          this.bigImgSrc = imgValue[imgIndex].path;
          imgIndex--;
        }
        if(imgIndex==-1){
          imgIndex = imgValue.length-1;
          // imgIndex = 3;
        }
      }
      if (e.wheelDelta < 0 || e.detail < 0) {
        //当滑轮向下滚动时
        if(imgValue.length>imgIndex){
          this.bigImgSrc = imgValue[imgIndex].path;
          imgIndex++;
          // ;
        }
        if(imgValue.length==imgIndex){
          imgIndex = 0;
        }
      }
    },
    mouseover(val){
      this.bigImgSrc = val[0].path;
    },
    bodyMouseWheel(e){
      e.preventDefault();
    },
    //删除工单
    deleteWorkOrder(){
      let id = [];
      if(this.multipleSelection.length<=0){
        this.$message({
          message: '请选择需要删除的工单',
          type: 'warning'
        });
      }else {
        for (let index = 0; index < this.multipleSelection.length; index++) {
          const element = this.multipleSelection[index];
          id.push(element.id);
        }
        this.postRequest(`/v1/orderReturnDelete?id=${id.join(',')}`)
        .then(res => {
          if(res.data.data=='1'){
            this.$message({
              message: '删除成功',
              type: 'success'
            });
            this.initData()
          }else {
            this.$message.error('删除失败')
          }
        }).catch(error => {
        })
      }
    },
    handleSizeChange(val) {
      this.$store.commit('CHANGE_RETURNSHOP_LIMIT',val);
    },
   //改变页码
    handleCurrentChange(val) {
      this.$store.commit('CHANGE_RETURNSHOP_PAGE',val);
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //上传图片函数
    postImgRequest(prams){
      if(this.imageList.length>0){
        let formData = new FormData();
        let _this = this;
        formData.append('files',this.files);
        for (let index = 0; index < _this.imageList.length; index++) {
          const element = _this.imageList[index].raw;
          formData.append('image[]',element);
        }
        _this.imgRequest('/v1/upLoadimgs',formData)
        .then(res => {
          // ;
          if(res.data.code=='200'){
            if(prams=='add'){
              _this.ruleForm.image = res.data.data;
            }
            if(prams=='change'){
              if(_this.ruleForm.refunds_imgs==''){
                _this.ruleForm.refunds_imgs = res.data.data;
              }else {
                _this.ruleForm.refunds_imgs += `,${res.data.data}`;
              }
            }
          }
        })
        .then(() => {
          if(prams=='add'){
            _this.postRequest(`/v1/orderReturnAdd?u_id=${sessionStorage.getItem('selectUId')}`,_this.ruleForm)
            .then(res => {
              // ;
              this.dialogFormVisible = false;
              if(res.data.data.status=='1'){
                _this.$message({
                  message: '添加成功',
                  type: 'success'
                });
                _this.initData();
                _this.ruleForm = {};
              }else {
                _this.$message.error(res.data.data.data);
              }
            }).catch(error => {
            })
          }
          if(prams=='change'){
            _this.postRequest('/v1/orderReturnEdit',_this.ruleForm)
            .then(res => {
              this.dialogFormVisible = false;
              if(res.data.data.status=='1'){
                _this.$message({
                  message: '修改成功',
                  type: 'success'
                });
                _this.initData();
                _this.ruleForm = {};
              }else {
                _this.$message.error(res.data.data.data);
              }
            }).catch(error => {
            })
          }
        })
        .catch(error => {
        })
      }else {
        this.ruleForm.image = '';
      }
    },
    //提交修改/添加的表单
    submit(prams){
      if(prams=='add'){
        this.addForm('ruleForm');
      }
      if(prams=='change'){
        this.changeForm('ruleForm');
      }
    },
    //添加退货订单按钮
    addForm(formName,u_id=sessionStorage.getItem('selectUId')) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // ;
          if(this.imageList.length!=0){
            this.postImgRequest('add');
          }else {
            this.dialogFormVisible = false;
            // ;
            this.postRequest(`/v1/orderReturnAdd?u_id=${u_id}`,this.ruleForm)
            .then(res => {
              // ;
              if(res.data.data.status=='1'){
                this.$message({
                  message: '添加成功',
                  type: 'success'
                });
                this.initData();
                this.ruleForm = {};
              }else {
                this.$message.error(res.data.data.data);
              }
            }).catch(error => {
            })
          }
        } else {
          return false;
        }
      });
    },
    //修改订单数据
    changeForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(this.imageList.length!=0){
            this.postImgRequest('change');
          }else {
            this.dialogFormVisible = false;
            this.postRequest('/v1/orderReturnEdit',this.ruleForm)
            .then(res => {
              // ;
              if(res.data.data.status=='1'){
                this.$message({
                  message: '修改成功',
                  type: 'success'
                });
                this.initData();
                this.ruleForm = {};
              }else {
                this.$message.error(res.data.data.data);
              }
            }).catch(error => {
            })
          }
        } else {
          return false;
        }
        // ;
      });
    },
    //初始化数据
    initData(){
      this.tableLoading = true
      this.postRequest(`/v1/orderReturnList`,this.$store.state.returnShopData)
      .then(res => {
        this.tableData = res.data.data.data;
        this.total = res.data.data.total;
        this.productNum = res.data.data.count;
        this.bigImgSrc = res.data.data.data[0] ? res.data.data.data[0].goods_imgs[0].path : '';
        this.tableLoading = false
      })
    }
  },
  created(){
    this.initData();
  },
  computed: {
    limit(){
      return this.$store.state.returnShopData.limit;
    },
    sku(){
      return this.$store.state.returnShopData.sku;
    },
    page(){
      return this.$store.state.returnShopData.page;
    },
    imgData(){
      return this.$store.state.imgListData;
    }
  },
  watch: {
    limit: function(newVal,oldVal){
      this.initData();
    },
    sku: function(newVal,oldVal){
      this.initData();
    },
    page: function(newVal,oldVal){
      this.initData();
    },
    imgData: function(newVal,oldVal){
      this.imageList = newVal;
      let successUrl = this.imageList.filter(item => {
        return item.status == 'success';
      })
      if(successUrl[0].url){
        this.ruleForm.refunds_imgs = successUrl[0].url;
      }else {
        this.ruleForm.refunds_imgs = '';
      }
    }
  },
  components: {
    uploadFile
  }
}
</script>


