/*账户组件*/
<template>
  <el-select v-model="value" placeholder="账户" size="mini" @change="handleClickUId">
    <el-option
      v-for="item in accountList"
      :key="item.id"
      :label="item.username"
      :value="item.id">
    </el-option>
  </el-select>
</template>

<script>
  export default {
    data() {
      return {
        accountList: [],//子账户列表
        value: ''
      }
    },
    methods: {
      getUserList(u_id=sessionStorage.getItem('userId')){
        this.postRequest(`/v1/merchantLists?u_id=${u_id}`)
        .then(res => {
          if(res.data.code=='200'){
            this.accountList = res.data.data;
            // this.value = this.accountList[0].username;
          }
        })
      },
      handleClickUId(value){
        this.$store.commit('CHANGE_SELECT_USER_ID',value);
        sessionStorage.setItem('selectUId',value);
      }
    },
    created(){
      this.getUserList();
    },
  }
</script>