<template>
  <div class="myWorkOrderTop">
    <div class="searchCon">
        <!-- <span>SKU：</span> -->
      <div class="inputCon">
        <el-input placeholder="请输入SKU" v-model="searchValue" :value="inputValue" class="input-with-select" suffix-icon="el-icon-search"
        @change='handleClick'>
          <!-- <el-button @click="handleClick" slot="append" icon="el-icon-search"></el-button> -->
        </el-input>
      </div>
    </div>
  </div>
</template>

<script>
  import accountCom from '@/components/OptionalCharges/accountCom'
export default {
  data(){
    return {
      inputValue: '',//输入框value
      searchValue: '',
    }
  },
  methods: {
    handleClick(){
      if(this.inputValue != this.searchValue){
        this.$store.commit('CHANGE_RETURNSHOP_SKU',this.searchValue);
        this.searchValue = '';
      }else {
        return false;
      }
    }
  }
}
</script>

