<template>
  <div class="uploadFileCon">
    <el-upload
      ref="upload"
      :action="uploadFileData.uploadSrc"
      list-type="picture-card"
      :multiple="uploadFileData.multiple"
      :auto-upload="false"
      :limit="uploadFileData.limit"
      :file-list="uploadFileData.imgList"
      :on-remove="handleRemove"
      :on-success="handleSuccess"
      :on-exceed="handleExceed"
      :on-change="handleChange"
      >
      <!-- 上传图片取消预览功能 -->
      <!-- :on-preview="handlePictureCardPreview" -->
      <i class="el-icon-plus"></i>
    </el-upload>
    <el-button v-show="uploadFileData.buttonState" style="margin: 10px;" size="small" type="success" @click="submitUpload" >上传到服务器</el-button>
    <!-- <el-button style="margin: 10px;" size="small" type="success" @click="submitUpload" >上传到服务器</el-button> -->
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" v-lazy="dialogImageUrl" alt="">
    </el-dialog>
  </div>
</template>
<script>
  export default {
    props: ['uploadFileData'],
    data() {
      return {
        dialogImageUrl: '',
        dialogVisible: false,
        fileList:[]
      };
    },
    methods: {
      handleRemove(file, fileList) {
      },
      // handlePictureCardPreview(file) {
        // this.dialogImageUrl = file.url;
        // this.dialogVisible = true;
      // },
      handleExceed(){
        this.$message({
          type: 'warning',
          message: `文件不能超过${this.uploadFileData.limit}哦~`
        })
      },
      handleChange(file, fileList){
        this.fileList = fileList;
        if(this.uploadFileData.comName=='returnManagement'){
          this.$store.commit('CHANGE_RETURNSHOP_IMGDATA',fileList);
        }
        if(this.uploadFileData.comName=='imgList'){
          this.$store.commit('CHANGE_IMGLIST_IMAGEDATA',fileList);
        }
        if(this.uploadFileData.comName=='fileCom'){
          this.$store.commit('CHNAGE_FILE_DATA',fileList);
        }
      },
      //手动提交
        submitUpload: function() {//自定义的上传图片的方法  
          // 1. 创建formData 利用AXIOS传递  
          let formData = new FormData();
          let _this = this;
          formData.append('files',this.files);
          for (let index = 0; index < _this.fileList.length; index++) {
            const element = _this.fileList[index].raw;
            formData.append('image[]',element);
          }
          this.imgRequest('/v1/upLoadimgs',formData)
          .then(res => {
          }).catch(error => {
          })
          // this.$refs.upload.submit(); //调用submit方法  
      },
      handleSuccess(){
      }
    },
  }
</script>